/* Address model
 * Detailed info http://guides.spreecommerce.org/developer/addresses.html
 * Public API's http://guides.spreecommerce.org/api/addresses.html
 */

export class Address {
  id?: string;
  firstname: string;
  lastname: string;
  full_name?: string;
  address1: string;
  address2: string;
  city: string;
  zipcode: string;
  phone: string;
  company: string;
  alternative_phone: string;
  country_id: string;
  state_id: string;
  state_name: string;
  state_text?: string;

  created_at?: string;
  updated_at?: string;
}

export function getFullAddress(address: Address): string {
  return address.address1 + ' ' +
    address.address2 + ' ' +
    address.city + ' ' +
    (address.state_text ? address.state_text : '') + ' ' +
    address.zipcode;
}



