import { AppState } from './../../interfaces';
import { createSelector } from 'reselect';
import { UserState } from './user.state';
import { List } from 'immutable';
import { LightOrder } from '../../core/models/order';
import { User } from '../../core/models/user';
import { Country } from '../../core/models/country';

// Base product state function
/**
 *
 *
 * @param {AppState} state
 * @returns {UserState}
 */
function getUserState(state: AppState): UserState {
    return state.users;
}

// ******************** Individual selectors ***************************
/**
 *
 *
 * @param {UserState} state
 * @returns {LightOrder[]}
 */
const fetchUserOrders = function(state: UserState): LightOrder[] {
    console.log("state2", state.orders.toJS());
    return state.orders.toJS();
};

/**
 * 
 * 
 * @param {UserState} state
 * @returns {User}
 */
const fetchCurrentUser = function(state: UserState): User {
    return state.user;
}

/**
 * 
 * 
 * @param {UserState} state
 * @returns {Country[]}
 */
const fetchCountries = function(state: UserState): Country[] {
    return state.countries.toJS();
}


// *************************** PUBLIC API's ****************************
export const getUserOrders = createSelector(getUserState, fetchUserOrders);
export const getUser = createSelector(getUserState, fetchCurrentUser);
export const getCountries = createSelector(getUserState, fetchCountries);




