// Core
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

// Services
import { CheckoutService } from './core/services/checkout.service';

// Store
import { Store } from '@ngrx/store';
import { AppState } from './interfaces';

import { ProductActions } from './product/actions/product-actions';
import { MenuActions } from './layout/actions/menu.actions';
import { UserActions } from './user/actions/user.actions';
import { AuthActions } from './auth/actions/auth.actions';

import { getAuthStatus } from './auth/reducers/selectors';
import { getTaxonomies } from './product/reducers/selectors';

// RX
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, OnDestroy {
  orderSub$: Subscription;
  taxonomies$: Observable<any>;
  currentUrl: string;
  currentStep: string;
  isAuthenticated: boolean;
  checkoutUrls = [
    '/checkout/cart',
    '/checkout/address',
    '/checkout/delivery',
    '/checkout/payment'
  ];
  constructor(
    private router: Router,
    private checkoutService: CheckoutService,
    private store: Store<AppState>,
    private authActions: AuthActions,
    private productActions: ProductActions,
    private menuActions: MenuActions,
    private userActions: UserActions
    ) {
    router
      .events
      .filter(e => e instanceof NavigationEnd)
      .subscribe((e: NavigationEnd) => {
        this.currentUrl = e.url;
        this.findCurrentStep(this.currentUrl);
        window.scrollTo(0, 0);
      });

    this.taxonomies$ = this.store.select(getTaxonomies);
    this.store.dispatch(this.authActions.authorize());
    this.store.dispatch(this.productActions.getAllTaxonomies());
    this.store.dispatch(this.productActions.getAllProducts());
    this.store.dispatch(this.menuActions.getAllDropDowns());
    this.store.dispatch(this.menuActions.getAllLinkPages());
  }

  ngOnInit() {
    this.store.select(getAuthStatus)
    .subscribe((isAuth) => {
      if (isAuth){
        this.store.dispatch(this.userActions.getUser());
        this.orderSub$ = this.checkoutService.fetchCurrentOrder()
          .subscribe();
        this.store.dispatch(this.userActions.getUserOrders());
      }
    }); 
  }

  isCheckoutRoute() {
    if (!this.currentUrl) {
      return false;
    }
    const index = this.checkoutUrls.indexOf(this.currentUrl);
    if (index >= 0) {
      return true;
    } else {
      return false;
    }
  }
  private findCurrentStep(currentRoute) {
    const currRouteFragments = currentRoute.split('/');
    const length = currRouteFragments.length;
    this.currentStep = currentRoute.split('/')[length - 1];
  }

  ngOnDestroy() {
    this.orderSub$.unsubscribe();
  }

}



