/**
 * TODO: move API key to config file
 * TODO: investigate the users of ProductActions / SearchActions; justify its placement in AppModule.providers
 * TODO: remove StoreDevtoolsModule depending on environment
 */

// Core
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

// Components
import { AppComponent } from './app.component';
import { CheckoutHeaderComponent } from './layout/checkout-header/checkout-header.component';
import { CheckoutFooterComponent } from './layout/checkout-footer/checkout-footer.component';
import { AdminComponent } from './admin/admin.component';

// Routes
import { RouterModule } from '@angular/router';
import { routes } from './app.routes';

// Store
import { StoreModule } from '@ngrx/store';
import { ProductActions } from './product/actions/product-actions';
import { SearchActions } from './home/reducers/search.actions';
import { reducer } from './app.reducers';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';

// Modules
import { CoreModule } from './core/index';
import { SharedModule } from './shared/index';
import { LayoutModule } from './layout/index';
import { HomeModule } from './home/index';

// Modules - Custom
import { BlogModule } from './blog/blog.module';

// Modules - Utility
import { NguiMapModule} from '@ngui/map';
import { AngularFontAwesomeModule } from 'angular-font-awesome';

// RX
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/first';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/finally';
import 'rxjs/add/observable/of';
import 'rxjs/add/observable/combineLatest';

@NgModule({
  declarations: [
    AppComponent,
    CheckoutHeaderComponent,
    CheckoutFooterComponent,
    AdminComponent,
  ],
  imports: [
    // Core
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    // DynamicComponentModuleFactory.buildModule([
    //   FormsModule
    // ]),

    // Routes
    RouterModule.forRoot(routes),

    // Store
    StoreModule.provideStore(reducer),
    StoreDevtoolsModule.instrumentOnlyWithExtension({
      maxAge: 15
    }),

    // Modules - Global
    CoreModule,
    SharedModule,
    LayoutModule,

    // Modules - Eagerly-loaded
    BlogModule,

    // Modules - Utility
    NguiMapModule.forRoot({
      apiUrl: 'https://maps.google.com/maps/api/js?key=AIzaSyDxyTHZsBeo1IDPnR_VSw8p9Yxg9Eb8PnQ'
    }),
    AngularFontAwesomeModule
  ],
  providers: [
    ProductActions,
    SearchActions
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



