import { Observable } from 'rxjs/Observable';
import { HttpService } from './http';
import { Injectable } from '@angular/core';

@Injectable()
export class ProductService {

  /**
   * Creates an instance of ProductService.
   * @param {HttpService} http
   *
   * @memberof ProductService
   */
  constructor(private http: HttpService) { }

  /**
   *
   *
   * @param {string} id
   * @returns {Observable<any>}
   *
   * @memberof ProductService
   */
  getProduct(id: string): Observable<any> {
    return this.http.get(`/api/products/${id}`)
    .map(res => res.json());
  }

  /**
   *
   *
   * @returns {*}
   *
   * @memberof ProductService
   */
  getTaxonomies(): any {
    return this.http.get(`/spree/api/v1/taxonomies?set=nested`)
    .map(res =>{ 
      this.setTaxonsInLocalStorage(res.json());
      return res.json();
    });
  }

  /**
   *
   *
   * @returns {*}
   *
   * @memberof ProductService
   */
  getProducts(): any {
    return this.http.get(`/api/products`)
    .map(res => res.json());
  }
  
  private setTaxonsInLocalStorage(data): void {
    if (data.count > 0){
      const taxons = JSON.stringify(data.taxonomies[0].root.taxons);
      localStorage.setItem('taxons', taxons);
    }
  }
}



