//Core
import { Component, OnInit, ChangeDetectionStrategy, Input } from '@angular/core';

//RX
import { Observable } from 'rxjs/Observable';

//Store
import { Store } from '@ngrx/store';
import { AppState } from '../../interfaces';

import { SearchActions } from './../../home/reducers/search.actions';

import { getAuthStatus } from '../../auth/reducers/selectors';
import { getAllDropDowns } from './../reducers/selectors';
import { getTotalCartItems } from './../../checkout/reducers/selectors';
import { getUser } from '../../user/reducers/selector';

//model
import { DropDown } from './../../core/models/drop_down';
import { User } from './../../core/models/user';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class HeaderComponent implements OnInit {
  isAuthenticated$: Observable<boolean>;
  totalCartItems$: Observable<number>;
  dropDowns$: Observable<DropDown>;
  user$: Observable<User>;
  @Input() taxonomies: any;
  constructor(
    private store: Store<AppState>,
    private searchActions: SearchActions,
  ) {
  }

  ngOnInit() {
    this.isAuthenticated$ = this.store.select(getAuthStatus);
    this.totalCartItems$ = this.store.select(getTotalCartItems);
    this.dropDowns$ = this.store.select(getAllDropDowns);
    this.user$ = this.store.select(getUser);
  }
}



