import { Action } from '@ngrx/store';
import { LightOrder } from '../../core/models/order';
import { User } from '../../core/models/user';
import { Country } from '../../core/models/country';

export class UserActions {
  static GET_USER_ORDERS = 'GET_USER_ORDERS';
  static GET_USER_ORDERS_SUCCESS = 'GET_USER_ORDERS_SUCCESS';
  static GET_CURRENT_USER = 'GET_CURRENT_USER';
  static GET_CURRENT_USER_SUCCESS = 'GET_CURRENT_USER_SUCCESS';
  static UPDATE_USER_ORDERS = 'UPDATE_USER_ORDERS';
  static UPDATE_USER_ORDERS_SUCCESS = 'UPDATE_USER_ORDERS_SUCCESS';
  static GET_COUNTRIES = 'GET_COUNTRIES';
  static GET_COUNTRIES_SUCCESS = 'GET_COUNTRIES_SUCCESS';

  getUserOrders(): Action {
    return { type: UserActions.GET_USER_ORDERS };
  }

  getUserOrdersSuccess(orders: LightOrder[]): Action {
    return { type: UserActions.GET_USER_ORDERS_SUCCESS, payload: orders };
  }

  getUser(): Action {
    return { type: UserActions.GET_CURRENT_USER};
  }

  getUserSuccess(user: User): Action {
    return { type: UserActions.GET_CURRENT_USER_SUCCESS, payload: user };
  }

  updateOrders(orderNumber: string): Action {
    return { type: UserActions.UPDATE_USER_ORDERS, payload: orderNumber };
  }

  updateOrdersSuccess(order: LightOrder): Action {
    return { type: UserActions.UPDATE_USER_ORDERS_SUCCESS, payload: order };
  }

  getCountries(): Action {
    return { type: UserActions.GET_COUNTRIES };
  }

  getCountriesSuccess(countries: Country[]) {
    return { type: UserActions.GET_COUNTRIES_SUCCESS, payload: countries };
  }
}



