import { DropDown } from './../../core/models/drop_down';
import { AppState } from './../../interfaces';
import { MenuState } from './menu.state';
import { createSelector } from 'reselect';
import { Map, List, fromJS } from 'immutable';

export function getMenuState(state: AppState): MenuState {
    return state.menu;
}

export function fetchDropDowns(state: MenuState) {
    return state.drop_downs.toJS();
}

export function fetchLinkPages(state: MenuState) {
    return state.link_pages.toJS();
}


export const getAllDropDowns = createSelector(getMenuState, fetchDropDowns);
export const getAllLinkPages = createSelector(getMenuState, fetchLinkPages);


