import { DropDown } from './../../core/models/drop_down';
import { MenuState, MenuStateRecord } from './menu.state';
import { Action, ActionReducer } from '@ngrx/store';
import { MenuActions } from './../actions/menu.actions'

export const initialState: MenuState = new MenuStateRecord() as MenuState;

export const menuReducer: ActionReducer<MenuState> = 
    (state: MenuState = initialState, { type, payload }: Action): MenuState => {
        switch (type) {
            case MenuActions.GET_ALL_DROP_DOWNS_SUCCESS:
                return state.merge({
                    drop_downs: payload
                }) as MenuState;
            case MenuActions.GET_ALL_LINK_PAGES_SUCCESS:
                return state.merge({
                    link_pages: payload
                }) as MenuState;
            // case MenuActions.GET_SELECTED_DROP_DOWN_SUCCESS:
            //     return state.merge({
            //         selected_page_id: payload
            //     }) as MenuState;

            default:
                return state;
        }
}



