import { Effect } from '@ngrx/effects';
import { OptionValue } from './../models/option_value';
import { element } from 'protractor';
import { Variant } from './../models/variant';
import { OptionType } from './../models/option_type';
import { Injectable } from '@angular/core';

/**Custom Interface for return option hash */
interface OptionTypesHash {
  [id: string]: {
    [id: string]: {
      optionValue: {},
      variantIds: Array<any>;
    }
  };
}

interface OptionHash {
  [id: string]: {
    optionCase: number,
    childOptions: {
      [id: string]: {
        optionCase: number,
        childOptions: {}
      }
    }
  }
}

interface VariantOptionHash {
  [id: string]: {
      [id: string]: {
        id: number,
        price: number
      }
  }
}

@Injectable()
export class VariantParserService {
  currVariantOptionValues: any;
  constructor() { }

  getVariantOptionHash(optionTypes: any[], variantOptionTypes: any[]){
    const variantOptionHash ={};

    for (let i = 0; i < optionTypes.length; i ++){
      if (optionTypes[i].option_case.name == 'Selection'){    //if selection option 
        variantOptionHash[optionTypes[i].name] = Object.assign({}, this.getVariantValueHash(optionTypes[i], variantOptionTypes))
      }
    }
    return variantOptionHash;
  }

  getVariantValueHash(optionType: any, variantOptionTypes: any[]){
    let innerHash = {};
    for(let i = 0; i < variantOptionTypes.length; i++){
      if (variantOptionTypes[i].option_type_id == optionType.id){
        let values = variantOptionTypes[i].product_variant_values;
        for(let j = 0; j < values.length; j ++){
          innerHash[values[j].name] = Object.assign({}, {
            id: optionType.option_values[j].id,
            price: values[j].price
          })
        }
      }
    }
    return innerHash;
  }
}




