import { Center } from '../../../core/models/center';
import { Country } from '../../../core/models/country';
import { Action } from '@ngrx/store';

export class CenterActions {
  static GET_ACTIVATED_CENTERS = 'GET_ACTIVATED_CENTERS';
  static GET_ACTIVATED_CENTERS_SUCCESS = 'GET_ACTIVATED_CENTERS_SUCCESS';
  static GET_COUNTRY = 'GET_COUNTRY';
  static GET_COUNTRY_SUCCESS = 'GET_COUNTRY_SUCCESS';

  getActivatedCenters(): Action {
    return {
      type: CenterActions.GET_ACTIVATED_CENTERS
    };
  }

  getActivatedCentersSuccess(centers: Array<Center>): Action {
    return {
      type: CenterActions.GET_ACTIVATED_CENTERS_SUCCESS,
      payload: centers
    };
  }

  getCountry(): Action {
    return {
      type: CenterActions.GET_COUNTRY
    };
  }

  getCountrySuccess(country: Country): Action {
    return {
      type: CenterActions.GET_COUNTRY_SUCCESS,
      payload: country
    };
  }
}



