import { DropDown } from './../../core/models/drop_down';
import { Injectable } from '@angular/core';
import { Effect, Actions } from '@ngrx/effects';
import { Observable } from 'rxjs/Observable';
import { MenuActions } from './../actions/menu.actions';

import { MenuService } from './../../core/services/menu.service';

import { Action } from '@ngrx/store';

@Injectable()
export class MenuEffects {
    constructor(
        private actions$: Actions,
        private menuService: MenuService,
        private menuActions: MenuActions
    ){}

    @Effect()
        GetAllDropDowns$: Observable<Action> = this.actions$
        .ofType(MenuActions.GET_ALL_DROP_DOWNS)
        .switchMap((action: Action) => this.menuService.getAllDropDowns())
        .map((data: any) => this.menuActions.getAllDropDownsSuccess(data));
    @Effect()
        GetAllLinkPages$: Observable<Action> = this.actions$
        .ofType(MenuActions.GET_ALL_LINK_PAGES)
        .switchMap((action: Action) => this.menuService.getLinkPages())
        .map((data: any) => this.menuActions.getAllLinkPagesSuccess(data));        
    // @Effect()
    //     GetSelectedDropDown$: Observable<Action> = this.actions$
    //     .ofType(MenuActions.GET_SELECTED_DROP_DOWN)
    //     .switchMap((action: Action) => this.menuService.getDropDownPage(action.payload))
    //     .map((data: any) => this.menuActions.getSelectedDropDownSuccess(data));
}


