import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState } from './../../interfaces';
import { getAllLinkPages } from './../../layout/reducers/selectors';
import { Observable } from 'rxjs/Observable';
import { Page } from './../../core/models/page';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { environment } from './../../../environments/environment';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-static-page',
  templateUrl: './static-page.component.html',
  styleUrls: ['./static-page.component.scss']
})
export class StaticPageComponent implements OnInit {
  linkPages$: Observable<Page>;
  link_from: string;
  dynamicExtraModules = [FormsModule];
  self = this;
  currentUrl: string;

  constructor(
    private store: Store<AppState>,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) {
    router
    .events
    .filter(e => e instanceof NavigationEnd)
    .subscribe((e: NavigationEnd) => {
      this.currentUrl = e.url;
      this.findCurrentStep(this.currentUrl);
      window.scrollTo(0, 0);
    });
  }

ngOnInit() {
    this.linkPages$ = this.store.select(getAllLinkPages);
    this.link_from = this.activatedRoute.snapshot.params.id;
  }

  isPresent(){
    let isPresent = false;
    this.linkPages$.subscribe((pages: any) => {
      for(let page of pages){
        if (this.getLink(page.link) == this.link_from) isPresent = true;
      }
    });
    return isPresent;
  }

  getLink(link: string){
    return link.replace('blog/page/', '');
  }

  onNavigate(str){
    this.router.navigate([str]);
  }
  
  private findCurrentStep(currentRoute) {
    this.link_from = currentRoute.replace('/blog/page/', '')
  }

}



