import { NgModule } from '@angular/core';

import { SharedModule } from '../../shared/index';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { ReactiveFormsModule } from '@angular/forms';
import { NguiMapModule } from '@ngui/map';

import { CenterComponent } from './center.component';
import { CenterMapComponent } from './components/center-map/center-map.component';
import { CenterApplicationComponent } from './components/center-application/center-application.component';
import {CenterApplicationSuccessComponent} from './components/center-application-success/center-application-success.component';

@NgModule({
  imports: [
    SharedModule,
    CommonModule,
    RouterModule,
    ReactiveFormsModule,
    NguiMapModule,
  ],
  declarations: [
    CenterComponent,
    CenterMapComponent,
    CenterApplicationComponent,
    CenterApplicationSuccessComponent
  ]
})
export class CenterModule { }



