import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { Effect, Actions } from '@ngrx/effects';

import { Action } from '@ngrx/store';
import { UserService } from '../services/user.service';
import { UserActions } from '../actions/user.actions';

import { AddressService } from '../../core/services/address.service';

@Injectable()
export class UserEffects {
  constructor(
    private actions$: Actions,
    private userService: UserService,
    private userActions: UserActions,
    private addrService: AddressService
  ) { }

  // tslint:disable-next-line:member-ordering
  @Effect()
    GetUserOrders$: Observable<Action> = this.actions$
    .ofType(UserActions.GET_USER_ORDERS)
    .switchMap(() => this.userService.getOrders())
    .filter((orders) => orders.length > 0)
    .map((orders) => this.userActions.getUserOrdersSuccess(orders));

  @Effect()
    GetUser$: Observable<Action> = this.actions$
    .ofType(UserActions.GET_CURRENT_USER)
    .switchMap(() => this.userService.getUser())
    .filter((user) => user != undefined)
    .map((user) => this.userActions.getUserSuccess(user));

  @Effect()
    UpdateOrders$: Observable<Action> = this.actions$
    .ofType(UserActions.UPDATE_USER_ORDERS)
    .switchMap((action: Action) => this.userService.getOrderDetail(action.payload))
    .filter((order) => order != undefined)
    .map((order) => this.userActions.updateOrdersSuccess(order));
  
  @Effect()
    GetCountries$: Observable<Action> = this.actions$
    .ofType(UserActions.GET_COUNTRIES)
    .switchMap((action: Action) => this.addrService.getCountries())
    .map(countries => this.userActions.getCountriesSuccess(countries));
}



