import { Component, OnDestroy, OnInit } from '@angular/core';

import { CenterService } from '../../../../core/services/center.service';
import { Center } from '../../../../core/models/center';
import { getFullAddress } from '../../../../core/models/address';

import { Subscription } from 'rxjs/Subscription';

import { Store } from '@ngrx/store';
import { AppState } from '../../../../interfaces';
import { getCountry } from '../../reducers/center.selectors';

@Component({
  selector: 'app-center-application-success',
  templateUrl: './center-application-success.component.html',
  styleUrls: ['./center-application-success.component.scss']
})
export class CenterApplicationSuccessComponent implements OnInit, OnDestroy {
  completedCenterApp: Center = null;

  getFullAddress = getFullAddress;

  constructor(private centerService: CenterService,
              private store: Store<AppState>) {
  }

  ngOnInit(): void {
    this.completedCenterApp = this.centerService.completedCenterApp;

    this.store.select(getCountry)
      .first((value: any) => value.toJSON().states)
      .filter((value: any) => value && value.toJSON().states && value.toJSON().states.length > 0)
      .subscribe( (value: any) => {
        const country = value.toJSON();

        if (this.completedCenterApp) {
          this.completedCenterApp.address.state_text = country.states.find((state => state.id === +this.completedCenterApp.address.state_id )).name;
        }
    });
  }

  ngOnDestroy(): void {
    this.centerService.completedCenterApp = null;
  }
}



