import { Pipe, PipeTransform } from '@angular/core';
import { environment } from './../../../environments/environment';

@Pipe({
  name: 'toHtml'
})
export class ToHtmlPipe implements PipeTransform {

  transform(content: string): string {
    let complied_content = content;
    complied_content = content.replace(/\/ckeditor_assets/g, environment.API_ENDPOINT + '/ckeditor_assets');
    let array_str = complied_content.split("href=\"http://");
    let i = 1;
    complied_content = array_str[0];
    while (array_str[i]) {
         complied_content += "(click)=\"onNavigate\(\'" + array_str[i].replace("\"", "\'\)\"");
        i++;
    }
    return complied_content;
  }

}



