/**
 * TODO:
 * - handle error on createCenterApp
 */

import {Component, OnDestroy, OnInit} from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { Address } from '../../../../core/models/address';
import { State } from '../../../../core/models/state';
import { Center } from '../../../../core/models/center';

import { CenterService } from '../../../../core/services/center.service';

import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

import { Store } from '@ngrx/store';
import { AppState } from '../../../../interfaces';
import { getActivatedCenters, getCountry } from '../../reducers/center.selectors';

@Component({
  selector: 'app-center-application',
  templateUrl: './center-application.component.html',
  styleUrls: ['./center-application.component.scss']
})
export class CenterApplicationComponent implements OnInit, OnDestroy {
  centerAppForm: FormGroup;

  states: Array<State>;

  stateQuery = null;
  stateQueryAllowed = false;

  centers: Array<Center> = [];

  constructor(
    private router: Router,
    private formBuilder: FormBuilder,
    private centerService: CenterService,
    private store: Store<AppState>
  ) {}

  ngOnInit(): void {
    this.store.select(getActivatedCenters)
      .first(value => value.length > 0)
      .subscribe(centers => {
        this.centers = centers;
      });

    this.store.select(getCountry)
      .first((value: any) => value.toJSON().states)
      .subscribe((country: any) => {
        this.states = country.toJSON().states;
      });

    // create form
    this.centerAppForm = this.formBuilder.group({
      office_name: ['', Validators.required ],
      head_doctor: ['', Validators.required ],
      address_attributes: this.formBuilder.group({
        firstname: ['', Validators.required ],
        lastname: ['', Validators.required ],
        address1: ['', Validators.required ],
        address2: '',
        city: ['', Validators.required ],
        zipcode: ['', [ Validators.required, Validators.pattern(/^\d{5}$/i)] ],
        phone: ['', Validators.required ],
        state_id: ['', Validators.required ]
        // country_id: ['', Validators.required ] // Always USA
      }),
      email: ['', [ Validators.required, Validators.pattern(/^\S+@\S+$/i) ] ],  // use very basic regex
      website: '',
      num_of_doctors: ['1', [ Validators.required, Validators.pattern(/^[1-9][0-9]*$/i) ] ],
      num_of_staff: ['1', [ Validators.required, Validators.pattern(/^[1-9][0-9]*$/i) ] ],
      college: ['', Validators.required ],
      degree: ['', Validators.required ]
    });
  }

  /**
   * UI button method; Save the form.
   */
  save(): void {
    // create an immutable copy
    const formValue = this.centerAppForm.value;
    const newCenterApp = {
      office_name: formValue.office_name,
      head_doctor: formValue.head_doctor,
      address_attributes: {
        firstname: formValue.address_attributes.firstname,
        lastname: formValue.address_attributes.lastname,
        address1: formValue.address_attributes.address1,
        address2: formValue.address_attributes.address2,
        city: formValue.address_attributes.city,
        zipcode: formValue.address_attributes.zipcode,
        phone: formValue.address_attributes.phone,
        state_id: formValue.address_attributes.state_id,
        country_id: '232', // Always USA
      },
      email: formValue.email,
      website: formValue.website,
      num_of_doctors: formValue.num_of_doctors,
      num_of_staff: formValue.num_of_staff,
      college: formValue.college,
      degree: formValue.degree
    };

    this.centerService.createCenterApp(newCenterApp)
      .first()
      .subscribe(value => {
        this.router.navigate(['blog', 'center', 'application', 'success']);
      });
  }

  /**
   * UI button method; Reset the form.
   */
  revert(): void {
    this.centerAppForm.reset({
      office_name: '',
      head_doctor: '',
      address_attributes: new Address(),
      email: '',
      website: '',
      num_of_doctors: '1',
      num_of_staff: '1',
      college: '',
      degree: ''
    });
  }

  /**
   * Return true if an element has failed validation.
   * Utilizes getElementFromFG() to obtain elements from nested FormGroups.
   */
  hasError(input: string): boolean {
    const target = this.getElementFromFG(input, this.centerAppForm);
    return  target.invalid &&
      ( target.dirty ||
        target.touched );
  }

  /**
   * Retrieve an element from a FormGroup (nested or non-nested).
   * Example: to retrieve centerAppForm.address.address1, do:
   * getElementFromNestedFormGroup('address.address1', this.centerAppForm)
   */
  getElementFromFG(input: string, target: any): any {
    return input.split('.').reduce((obj, key) => {
      return (<FormGroup>obj).get(key);
    }, target);
  }

  onStateQueryKeyUp(event) {
    if (this.stateQuery && this.centers.length > 0) {
      const matchingCenters = this.centers.filter(center => center.address.zipcode === this.stateQuery.toString())
      this.stateQueryAllowed = matchingCenters.length === 0 ? true : false;
    }
  }

  ngOnDestroy(): void {
  }

}



