import { Injectable } from '@angular/core';
import { HttpService } from '../../core/services/http';
import { UserActions } from '../actions/user.actions';
import { Store } from '@ngrx/store';
import { AppState } from '../../interfaces';
import { LightOrder } from '../../core/models/order';
import { Response } from '@angular/http';
import { User, LightUser } from '../../core/models/user';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class UserService {

  constructor(
    private http: HttpService,
    private actions: UserActions,
    private store: Store<AppState>
  ) { }

  /**
   *
   *
   * @returns {Observable<LightOrder[]>}
   *
   * @memberof UserService
   */
  getOrders(): Observable<LightOrder[]> {
    return this.http.get('api/orders')
      .map((res: Response) => {console.log(res.json()); return res.json()});
  }

  /**
   *
   *
   * @param {any} orderNumber
   * @returns {Observable<LightOrder>}
   *
   * @memberof UserService
   */
  getOrderDetail(orderNumber): Observable<LightOrder> {
    console.log(`api/orders/${orderNumber}`);
    return this.http.get(`api/orders/${orderNumber}`)
      .map((res: Response) => res.json());
  }

  /**
   *
   *
   * @returns {Observable<User>}
   *
   * @memberof UserService
   */
  getUser(): Observable<User> {
    const user_id = JSON.parse(localStorage.getItem('user')).id;
    return this.http.get(`spree/api/v1/users/${user_id}`)
      .map(res => res.json());
  }


  ////////////

  getCorpUser(id): Observable<User> {
    return this.http.get(`api/corporate_account/corporate_users/${id}`)
      .map(res=> res.json());
  }

  getCorpUsers(): Observable<User[]> {
    return this.http.get(`api/corporate_account/corporate_users`)
      .map(res => res.json());
  }

  getCorpOrders(): Observable<LightOrder[]> {
    return this.http.get(`api/corporate_account/orders`)
      .map(res => res.json());
  }

  createCorpUser(param): Observable<any> {
    return this.http.post(`api/corporate_account/corporate_users`, param)
      .map(res => res.json());
  }

  updateCorpUser(id, param): Observable<any> {
    return this.http.put(`api/corporate_account/corporate_users/${id}`, param)
      .map(res => res.json());
  }

  deleteCorpUser(id): Observable<User> {
    return this.http.delete(`api/corporate_account/corporate_users/${id}`)
      .map(res => res.json());
  }

}



