import { Component, OnDestroy, OnInit } from '@angular/core';

import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subscription } from 'rxjs/Subscription';

import { Center } from '../../../../core/models/center';
import { getFullAddress } from '../../../../core/models/address';

import { Store } from '@ngrx/store';
import { AppState } from '../../../../interfaces';
import { getActivatedCenters, getCountry } from '../../reducers/center.selectors';

@Component({
  selector: 'app-center-map',
  templateUrl: './center-map.component.html',
  styleUrls: ['./center-map.component.scss']
})
export class CenterMapComponent implements OnInit, OnDestroy {
  centersWithAddress: Array<Center> = [];
  centersWithAddressFiltered: Array<Center> = [];
  centersWithAddressSub: Subscription;

  states = [];
  selectedState = '';
  selectedState$ = new BehaviorSubject('');

  getFullAddress = getFullAddress;

  marker = {
    center: null
  };

  constructor(
    private store: Store<AppState>
  ) {}

  ngOnInit() {
    const centers$ = this.store.select(getActivatedCenters);
    const country$ = this.store.select(getCountry);

    this.centersWithAddressSub = Observable.combineLatest([centers$, country$, this.selectedState$])
      .filter(value => value[0].length > 0)
      .filter(value => value[1] && value[1].toJSON().states && value[1].toJSON().states.length > 0)
      .subscribe( (value) => {
        // get values from observables
        const centers = value[0];
        const country = value[1].toJSON();
        const search = value[2];

        // init states
        if (this.states.length === 0) {
          this.states = country.states;
        }

        // init centersWithAddress
        this.centersWithAddress = centers.map(center => {
          const newCenter = center;
          newCenter.address.state_text = country.states.find((state => state.id === +center.address.state_id )).name;
          return newCenter;
        });

        // refresh: centersWithAddressFiltered
        if (search === '') {
          this.centersWithAddressFiltered = this.centersWithAddress;
        } else {
          this.centersWithAddressFiltered = this.centersWithAddress.filter(center => search === center.address.state_id.toString());
        }
    });
  }

  // Map: ready
  onMapReady(event: any) {
  }

  // Map: click
  onMapClick(event: any) {
    // console.log(event);
  }

  // Map: idle
  onIdle(event: any) {
    // console.log(event);
  }

  // Marker: initialized
  onMarkerInit(event: any) {
    // console.log(event);
  }

  onChange(event) {
    this.selectedState$.next(this.selectedState);
  }

  onMarkerClick(event, data) {
    this.marker.center = data;
    event.target.nguiMapComponent.openInfoWindow('infoWindow', event.target);
  }

  ngOnDestroy() {
    if (this.centersWithAddressSub) { this.centersWithAddressSub.unsubscribe(); }
  }

}



