import { AppState } from '../../../interfaces';
import { CenterState } from './center.state';
import { createSelector } from 'reselect';

// Base center state selector function
export function getCenterState(state: AppState): CenterState {
  return state.center;
}

// ******************** Individual selectors ***************************
export function fetchActivatedCenters(state: CenterState) {
  return state.centers.toJS();
}

export function fetchCountry(state: CenterState) {
  return state.country;
}

// *************************** PUBLIC API's ****************************
export const getActivatedCenters = createSelector(getCenterState, fetchActivatedCenters);
export const getCountry = createSelector(getCenterState, fetchCountry);



