import { Center } from '../../../core/models/center';
import { Country } from '../../../core/models/country';
import { CenterActions } from '../actions/center.actions';
import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { Effect, Actions } from '@ngrx/effects';

import { CenterService } from '../../../core/services/center.service';
import { Action } from '@ngrx/store';

@Injectable()
export class CenterEffects {
  constructor(private actions$: Actions,
              private centerService: CenterService,
              private centerActions: CenterActions) { }

  @Effect()
  GetActivatedCenters$: Observable<Action> = this.actions$
    .ofType(CenterActions.GET_ACTIVATED_CENTERS)
    .switchMap((action: Action) => this.centerService.getActivatedCenters())
    .map((data: Array<Center>) => this.centerActions.getActivatedCentersSuccess(data));

  @Effect()
  GetCountry$: Observable<Action> = this.actions$
    .ofType(CenterActions.GET_COUNTRY)
    .switchMap((action: Action) => this.centerService.getCountry())
    .map((data: Country) => this.centerActions.getCountrySuccess(data));
}



