import { Routes } from '@angular/router';
import { CenterComponent } from './center.component';
import { CenterMapComponent } from './components/center-map/center-map.component';
import { CenterApplicationComponent } from './components/center-application/center-application.component';
import { CenterApplicationSuccessComponent } from './components/center-application-success/center-application-success.component';
import { CanActivateViaCenterGuard } from '../../core/guards/center.guard';

export const routes: Routes = [
    {
      path: '', component: CenterComponent,
      children: [
        { path: '', redirectTo: 'map', pathMatch: 'full' },
        { path: 'map', component: CenterMapComponent },
        { path: 'application', component: CenterApplicationComponent },
        {
          path: 'application/success',
          component: CenterApplicationSuccessComponent,
          canActivate: [CanActivateViaCenterGuard]
        }
      ]
    }
];



