import { Action, ActionReducer } from '@ngrx/store';
import { UserState, UserStateRecord } from './user.state';
import { UserActions } from '../actions/user.actions';
import { LightOrder } from '../../core/models/order';

export const initialState: UserState = new UserStateRecord() as UserState;

export /**
 *
 *
 * @param {UserState} [state=initialState]
 * @param {Action} { type, payload }
 * @returns {UserState}
 */
const userReducer: ActionReducer<UserState> =
  (state: UserState = initialState, { type, payload }: Action): UserState => {
    switch (type) {
      case UserActions.GET_USER_ORDERS_SUCCESS:
        return state.merge({ orders: payload }) as UserState;
      
      case UserActions.GET_CURRENT_USER_SUCCESS:
        return state.merge({ user: payload }) as UserState;
      
      case UserActions.UPDATE_USER_ORDERS_SUCCESS:
        const _orders = state.orders.insert(0, payload)
        return state.merge({ orders: _orders}) as UserState;

      case UserActions.GET_COUNTRIES_SUCCESS:
        return state.merge({ countries: payload }) as UserState;

      default:
        return state;
    }
  };



