import { Address } from './../../core/models/address';
import { LineItem } from './../../core/models/line_item';
import { Shipment } from './../../core/models/shipment';
import { Map, Record, List, fromJS } from 'immutable';

export interface CheckoutState extends Map<string, any> {
  orderNumber: string;
  orderState: string;
  lineItemIds: List<number>;
  lineItemEntities: Map<number, LineItem>;
  totalCartItems: number;
  totalItemValue: number;
  totalCartValue: number;
  billAddress: any;
  shipAddress: any;
  shipmentEntities: Map<number, Shipment>;
  adjustmentOrder: string;
  isOwnShipping: boolean;
  selectedRateIndex: number;
  isUpdated: boolean;
}

export const CheckoutStateRecord = Record({
  orderNumber: null,
  orderState: null,
  lineItemIds: List([]),
  lineItemEntities: Map({}),
  totalCartItems: 0,
  totalItemValue: 0,
  totalCartValue: 0,
  billAddress: fromJS({}),
  shipAddress: fromJS({}),
  shipmentEntities: Map({}),
  adjustmentOrder: null,
  isOwnShipping: false,
  selectedRateIndex: 0,
  isUpdated: false
});



