import { CenterActions } from '../actions/center.actions';
import { CenterState, CenterStateRecord } from './center.state';
import { Action, ActionReducer } from '@ngrx/store';

export const initialState: CenterState = new CenterStateRecord() as CenterState;

export const centerReducer: ActionReducer<CenterState> =
  (state: CenterState = initialState, { type, payload }: Action): CenterState => {
    switch (type) {

      case CenterActions.GET_COUNTRY_SUCCESS:
        return state.merge({
          country: payload
        }) as CenterState;

      case CenterActions.GET_ACTIVATED_CENTERS_SUCCESS:
        return state.merge({
          centers: payload
        }) as CenterState;

      default:
        return state;
    }
  };



